* {
  box-sizing: border-box;
}

html {
  min-width: 576px;
}

a,
p,
span,
h1,
h2,
h3 {
  margin-block-start: 0;
  margin-block-end: 0.3em;
}

body {
  color: #665353;
}

h2 {
  padding: 40px;
  text-align: center;
  font-size: 30px;
}

section {
  max-width: 1200px;
  margin: auto;
  padding: 20px 40px;
}

.container_section {
  min-height: 100vh;
  padding-bottom: 233px;
}

.app {
  margin: 0;
}

.block_for_jakor {
  margin: -100px 0 100px 0;
}

a {
text-decoration: none;
}

/**slider***************************/

.slick-slider{
  margin: 0 40px;
}

.slick-list{
  max-width: 1120px;
  margin: auto;
  height: 250px;
  white-space: nowrap;
}

.slick-track div {
  max-width: 280px;
  min-width: 280px;
  outline: none;
}

/*media******************************************************************/


@media (max-width: 576px) {

  body {
      font-size: 20px;
  }

}