.product_img {
    justify-self: center;
    width: 250px;
    height: 250px;
    object-fit: cover;
    -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.5, transparent), to(rgba(0, 0, 0, 0.5)));
    border-top-left-radius: 32px;
    border-top-right-radius: 32px;
}

.product_title {
    font-size: 20px;
    font-weight: bold;
}

.product_item {
    border: 2px solid rgb(253, 196, 196);
    border-radius: 35px;
    padding: 30px 30px 130px 30px;
    position: relative;
    color: #473a3a;
    background-color: white;
}

.block_with_text_for_product {
    position: absolute;
    width: 250px;
    margin: auto;
}

.product_item p {
    margin-bottom: 5px;
    margin-top: 5px;
    text-align: center;
}

.product_price {
    display: flex;
    justify-content: center;
}

.product_price span {
    font-weight: bold;
    font-size: 20px;
    padding-top: 5px;
}

.plus_btn {
    background-color: white;
    border: 2px solid rgb(253, 196, 196);
    border-radius: 10px;
    padding: 10px 10px 7px 10px;
    margin-left: 10px;
    cursor: pointer;
}

.check_btn {
    background-color: rgb(91, 238, 115);
    border: 2px solid rgb(253, 196, 196);
    border-radius: 10px;
    padding: 10px 10px 7px 10px;
    margin-left: 10px;
    cursor: pointer;
}

.plus_btn:hover {
    background-color: rgb(253, 196, 196);
}

.plus_btn img,.check_btn img {
    width: 15px;
}

/* Кнопка Добавить в избранное */

.favorite_btn{
    border: 0;
    border-radius: 10px;
    background-color: #fab1b1;
    color: white;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
    transition: transform 0.5s ease-in-out;
    margin-bottom: 10px;
}

.favorite_btn:hover{
    transform: translateX(20px);
    background-color: #fd7a7a;
}
.favorite_btn_added {
    border: 0;
    border-radius: 10px;
    background-color: #3ff58b;
    color: white;
    padding: 7px;
    font-size: 14px;
    text-align: start;
    width: fit-content;
    cursor: pointer;
    margin-bottom: 10px;
}

.contentLoader{
    background-image: url(/public/img/Spinner-1s-200px.svg);
    background-size: 250px 250px;
    width: 250px;
    height: 250px;
}