

header {
    position: fixed;
    z-index: 1000;
    background: url("/public/img/header/fon.png") center;
    width: 100%;
    max-width: 1920px;
    height: 100px;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.7);
    top: 0;
}

.header_section {
    max-width: 1200px;
    margin: auto;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    height: 100px;
}

header img {
    transform: translateY(5px);
    padding: 0 20px;
}

nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.nav_item {
    text-align: center;
    color: white;
    font-size: 20px;
    text-decoration: none;
    padding: 0 20px;
}

.nav_item:hover {
    color: #fd7a7a;
    text-shadow: 0.5px 0.5px 0 #fd7a7a, -0.5px -0.5px 0 #fd7a7a, 0.5px -0.5px 0 #fd7a7a, -0.5px 0.5px 0 #fd7a7a;
    cursor: pointer;
}

.count_cart_item{
    font-size: 20px;
    font-weight: bold;
    background: white;
    color: red;
    border-radius: 50%;
    border: 1px solid white;
    padding: 0 9px 3px 9px;
    margin-left: 10px;
}