
.product_fon {
    background: repeating-linear-gradient(145deg,
            rgba(255, 224, 224, 0.2),
            rgba(255, 224, 224, 0.2),
            rgba(253, 176, 176, 0.3),
            rgba(253, 176, 176, 0.3) 160px)
}

.products {
    display: grid;
    justify-content: space-evenly;
    justify-items: center;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fill, 310px);
}

/* Поиск *********************************/

.search{
    display: flex;
    justify-content: center;;
}

.search h2 {
    font-size: 32px;
}

.search_block{
    border-radius: 10px;
    padding: 15px;
    width: 280px;
    background: #fab1b1;
}

.search_block img{
    width: 13px;
    margin-right: 15px;
}

.search_block input {
    border: none;
    font-size: 16px;
}

.search_block input:focus-visible {
    outline: none;
}