footer {
    background-color: black;
    margin-top: -233px;
}

.footer_section {
    max-width: 1200px;
    margin: auto;
    padding: 20px 40px;
    display: flex;
    justify-items: center;
    align-items: center;
}

footer p {
    color: white;
    text-align: right;
}

footer .logo {
    font-size: 45px;
    font-weight: bold;
    color: white;
    padding-right: 40px;
}

footer b {
    font-size: 30px;
}

/* media******************************************************************/

@media (max-width: 576px) {

    .footer_section {
        
        display: block;
    }

    .footer_section .logo {
        width: 100%;
        margin: 30px auto;
        display: flex;
        justify-content: center;
        padding-right: 0px;
    }

} 