/*o nas******************************************************************/

.o_nas_flex {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fill, minmax(400px, 48%));
    justify-content: space-around;
}

.o_nas_flex p {
    width: 100%;
}

.o_nas_flex img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px;
}

.text-section {
    max-width: 1200px;
    margin: 0 auto;
}


/*media******************************************************************/

@media (max-width: 992px) {

    .o_nas_flex {
        display: block;
    }

    .o_nas_flex p:first-child {
        padding-bottom: 30px;
    }

    .o_nas_flex img {
        width: 100%;
        height: 300px;
    }
}