/*banner(intro)******************************************************************/

.section_banner {
    background: url("/public/img/intro/intro.png") center;
    background-size: cover;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    width: 100%;
    height: 600px;
    padding: 140px 20px 20px 40px;
}

.banner {
    padding: 0px 60px;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 380px;
    border-radius: 30px;
    background: rgba(255, 255, 255, 0.5);
    position: relative;
}

.text_banner_one {
    color: #665353;
    font-size: 40px;
    font-weight: bold;
}

.text_banner_two {
    font-size: 30px;
    color: #746565;
}

.banner_btn {
    position: absolute;
    z-index: 5;
    background-color: #fd7a7a;
    color: white;
    padding: 20px 30px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 20px;
    max-width: 380px;
    width: 100%;
    margin-top: 10px;
    border: none;
    cursor: pointer;
    box-shadow: 0px 3px 0px 0px rgb(150, 84, 84),
        0px -3px 0px 0px rgb(253, 196, 196);
}

.banner_btn:hover {
    background-color: #ff6161;
}
