.slider_overflow {
    overflow: hidden;
}

.slider_img img {
    width: 272px;
    height: 250px;
    object-fit: cover;
    opacity: 0.8;
    border-radius: 30px;
    padding: 1px;
    cursor: pointer;
}

.slider_img img:hover {
    opacity: 1;
}

.slider_overflow h3 {
    text-align: center;
}